<template>
  <div class="key">
    <ul>
      <li v-for="(item, index) in keywordsList" :key="index" @click="doKeySearch(item)">
        <div class="left" v-html="formatHtml(item.key)"></div>
        <div class="right" v-if="item.tag.length">
          <div class="tag-item" v-for="(tag, i) in item.tag" :key="i" @click.stop="doKeySearch(tag)">{{ tag.tsh }}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'key-search',
  props: ['keyword', 'keywordsList'],
  data() {
    return {}
  },
  methods: {
    doKeySearch(item) {
      if (item.key) {
        // 点击li标签
        // 触发搜索
        this.$emit('doSearch', {
          keyword: item.key,
          type: 'key'
        })
      } else {
        // 点击tag标签
        if (item.linkType === '1') {
          // 跳商品详情
          this.$router.push({
            path: '/goodsDetail',
            query: {
              productId: item.link.split('=')[1],
            }
          })
        } else if (item.linkType === '4') {
          this.$router.push(`/bannerSubject?id=${item.link.split('&')[1].split('=')[1]}`)
        } else if (item.linkType === '5') {
          this.$router.push(`/bannerCustom?id=${item.link.split('/').slice(-1)}`)
        } else {
          // 触发搜索
          this.$emit('doSearch', {
            keyword: item.tse,
            type: 'key'
          })
        }
      }
    },
    formatHtml (value) {
      // 处理关键字
      return value.split(this.keyword).join(`<span style="color:#999">${this.keyword}</span>`)
    },
  },
}
</script>

